import './App.css';
import {useEffect, useRef, useState} from "react";
import Preview from "./Preview";
import ImageModal from "./ImageModal";

const iniPhoto = 'data:image/png;base64,';

const resetUserDetails = () => {
  return ({
    firstName: '',
    lastName: '',
    designation: '',
    phoneNumber: '',
    linkedInUsername: '',
    linkedInURL: '',
    photo: ''
  });
}

function App () {
  const [editTab, setEditTab] = useState(true);
  const [disablePreview, setDisablePreview] = useState(true);
  const [showModal, setShowModal] = useState(false);
  const userDetailData = resetUserDetails();
  const [userDetails, setUserDetails] = useState(userDetailData);

  const previewRef = useRef();

  const saveUserDetails = (data) => {
    setUserDetails((state) => {
      const userData = { ...state, ...data };
      modifyDisablePreview(userData);
      window.localStorage.setItem('userData', JSON.stringify(userData));
      return userData;
    });
  };

  const modifyDisablePreview = (data) => {
    const {firstName, designation, linkedInUsername, linkedInURL} = data;
    setDisablePreview(() => !(firstName && designation && linkedInUsername && linkedInURL));
  };

  const handleChange = (e, property) => {
    saveUserDetails({
      [property]: e.target.value
    });
  }

  const onPhotoAdded = (photo) => {
    saveUserDetails({photo})
    setShowModal(false);
  };

  const onClickNavItem = () => {
    setEditTab((isActive) => {
      return !isActive;
    });
  }

  const onCopiedToClipBoard = (alertText) => {
    alert(alertText);
  }

  const closeModal = () => {
    setShowModal(false);
  }

  useEffect(() => {
    let userData = window.localStorage.getItem('userData');
    if(userData) {
      userData = JSON.parse(userData);
      modifyDisablePreview(userData);
      setUserDetails(userData);
    } else {
      modifyDisablePreview(true);
    }
  }, []);

  return (
    <div className="App">
      <div className="container-wrap">
        <div className="container">
          <nav className="es-nav">
            <ul>
              <li className={editTab ? 'active' : ''}>
                <button onClick={onClickNavItem} className="es-nav-item">Edit</button>
              </li>
              <li className={editTab ? '' : 'active'}>
                <button onClick={onClickNavItem} className="es-nav-item" disabled={disablePreview}>Preview</button>
              </li>
            </ul>
          </nav>
          <main>
            {editTab ? (
              <div className="edit-wrapper">
                <div className="profile-wrap">
                  <div className="profile-img">
                    <img src={userDetails.photo || iniPhoto} alt=""/>
                  </div>
                  <div className="profile-btn">
                    <button
                      onClick={() => setShowModal(true)}
                      className="btn btn-profile" type="button">
                      Change Photo
                    </button>
                  </div>
                </div>
                <form action="">
                  <div className="form-control">
                    <label htmlFor="firstName" className="required">First Name</label>
                    <input type="text" value={userDetails.firstName} id="firstName" placeholder="Firstname"
                           onChange={(e) => handleChange(e, 'firstName')}/>
                  </div>
                  <div className="form-control">
                    <label htmlFor="lastName">Last Name</label>
                    <input type="text" value={userDetails.lastName} id="lastName" placeholder="Lastname"
                           onChange={(e) => handleChange(e, 'lastName')}/>
                  </div>
                  <div className="form-control">
                    <label htmlFor="designation" className="required">Designation</label>
                    <input type="text" value={userDetails.designation} id="designation" placeholder="Designation"
                           onChange={(e) => handleChange(e, 'designation')}/>
                  </div>
                  <div className="form-control">
                    <label htmlFor="phoneNumber">Phone Number</label>
                    <input type="phone" value={userDetails.phoneNumber} id="phoneNumber" placeholder="Phone Number"
                           onChange={(e) => handleChange(e, 'phoneNumber')}/>
                  </div>
                  <div className="form-control">
                    <label htmlFor="linkedInUsername" className="required">LinkedIn Username</label>
                    <input type="text" value={userDetails.linkedInUsername} id="linkedInUsername" placeholder="LinkedIn Username"
                           onChange={(e) => handleChange(e, 'linkedInUsername')}/>
                  </div>
                  <div className="form-control">
                    <label htmlFor="linkedInURL" className="required">LinkedIn Link</label>
                    <input type="text" value={userDetails.linkedInURL} id="linkedInURL" placeholder="LinkedIn URL"
                           onChange={(e) => handleChange(e, 'linkedInURL')}/>
                  </div>
                  <div className="btn-wrap">
                    <button className="btn btn-preview" type="button" disabled={disablePreview}
                            onClick={() => setEditTab(false)}>
                      Preview
                    </button>
                    <button className="btn btn-cancel" type="button"
                            onClick={() => setUserDetails(resetUserDetails())}>
                      Reset
                    </button>
                  </div>
                </form>
              </div>
            ) : (
              <div className="preview wrapper">
                <Preview ref={previewRef} userDetails={userDetails} onCopiedToClipBoard={onCopiedToClipBoard}/>
              </div>
            )}
          </main>
          {editTab ? '' : (<div className="btn-wrap btn-download-wrap">
            <button className="btn btn-download" style={{marginRight: '12px'}} type="button"
                    onClick={() => previewRef.current.copyDesign()}>
              Copy Design
            </button>
            <button className="btn btn-download" type="button"
                    onClick={() => previewRef.current.copyCode()}>
              Copy Code
            </button>
          </div>)}
        </div>
      </div>
      <div className={'modal-wrap ' + (showModal && 'active')}>
        <ImageModal onPhotoAdded={onPhotoAdded} closeModal={closeModal}/>
      </div>
    </div>
  );
}

export default App;
