import {forwardRef, useImperativeHandle, useRef} from "react";

const Preview = forwardRef(({ userDetails, onCopiedToClipBoard }, ref) => {
  const previewRef = useRef();
  const emailContainerRef = useRef();
  const { photo, firstName, lastName, designation, phoneNumber, linkedInUsername, linkedInURL } = userDetails;

  useImperativeHandle(ref, () => ({
    copyCode () {
      navigator.clipboard.writeText(previewRef.current.innerHTML)
        .then(() => {
          onCopiedToClipBoard('HTML code copied into clipboard!');
        })
        .catch((error) => {
          console.error('Error copying HTML content to clipboard:', error);
        });
    },
    copyDesign() {
      const emailContainer = emailContainerRef.current;
      if(emailContainer) {
        const range = document.createRange();
        range.selectNode(emailContainer);
        window.getSelection().removeAllRanges();
        window.getSelection().addRange(range);
        document.execCommand('copy');
        window.getSelection().removeAllRanges();
        onCopiedToClipBoard('Design copied into clipboard!');
      }
    }
  }));

  return (
    <div ref={previewRef}>
      <div ref={emailContainerRef}
        style={{ backgroundColor: "#fff", fontFamily: "sans-serif", color: "#333" }}>
        <table style={{ height: 1, padding: '10px' }}>
          <tbody>
          <tr>
            <td style={{ paddingRight: '10px' }}>
              <div>
                <div
                  style={{
                    width: 56,
                    height: 56,
                    overflow: "hidden",
                    borderRadius: "50%"
                  }}
                >
                  {photo ? <img
                    style={{ width: '56px', display: "block" }}
                    src={photo}
                    alt=""
                  /> : <div style={{width: '56px', height: '56px', color: 'transparent'}}>&nbsp;</div>}
                </div>
              </div>
              <div
                style={{
                  marginTop: '10px',
                  marginBottom: '2px',
                  fontSize: '20px',
                  fontWeight: 700
                }}
              >
                <span style={{ color: "#346b80" }}>{firstName}</span>&nbsp;
                <span style={{ color: "#346b80" }}>{lastName}</span>
              </div>
              <div
                style={{
                  color: "#888",
                  fontSize: '11px',
                  paddingBottom: '10px',
                  fontWeight: 500,
                  textTransform: "uppercase"
                }}
              >
                {designation}
              </div>
            </td>
            <td rowSpan={2} style={{ background: '#f80', padding: '0px 1px 0px 0px' }}>

            </td>
            <td style={{ paddingLeft: '18px'}}>
              <div style={{ marginBottom: '6px' }}>
                <a style={{ textDecoration: "none" }}
                   href="https://www.smarthub.ai/"
                   target="_blank">
                <img
                  width={'150px'}
                  style={{ display: "block" }}
                  src="data:image/png;base64,"
                  alt=""
                />
                </a>
              </div>
              <div style={{ marginBottom: '4px' }}>
                <a
                  style={{ textDecoration: "none" }}
                  href="https://www.linkedin.com/company/smarthub-ai"
                  target="_blank"
                >
                  <table style={{ height: 1 }}>
                    <tbody>
                    <tr>
                      <td>
                        <img
                          style={{ display: "block", width: '12px' }}
                          src="data:image/jpeg;base64,"
                          alt=""
                        />
                      </td>
                      <td>
                      <span
                        style={{
                          fontSize: '11px',
                          fontWeight: 700,
                          lineHeight: 1,
                          display: "block",
                          color: "#346b80"
                        }}
                      >
                        smarthub.ai
                      </span>
                      </td>
                    </tr>
                    </tbody>
                  </table>
                </a>
              </div>
            </td>
          </tr>
          <tr>
            <td>
              <div style={{ color: "#666" }}>
                <span style={{fontSize: '13px'}}>{phoneNumber}</span>
              </div>
              <div style={{ marginBottom: '4px' }}>
                <a
                  style={{ textDecoration: "none" }}
                  href={linkedInURL}
                  target="_blank"
                >
                  <table style={{ height: 1 }}>
                    <tbody>
                    <tr>
                      <td>
                        <img
                          style={{ display: "block", width: '12px' }}
                          src="data:image/jpeg;base64,"
                          alt=""
                        />
                      </td>
                      <td>
                      <span
                        style={{
                          fontSize: '11px',
                          fontWeight: 700,
                          lineHeight: 1,
                          display: "block",
                          color: "#346b80"
                        }}
                      >
                        {linkedInUsername}
                      </span>
                      </td>
                    </tr>
                    </tbody>
                  </table>
                </a>
              </div>
            </td>
            <td style={{ paddingLeft: '18px'}}>
              <div style={{ color: '#666', fontSize: '12px' }}>
                <div>Palo Alto, CA</div>
                <div>Seattle, WA</div>
                <div>Bengaluru, India</div>
              </div>
            </td>
          </tr>
          <tr>
            <td style={{ paddingTop: '14px' }}>
              <a
                style={{ textDecoration: "none", display: "inline-block" }}
                href="https://www.linkedin.com/posts/smarthub-ai_forbesindia-dglobalist-dgems2023-activity-7123183608080551936-j07Y/"
              >
                <img
                  style={{
                    height: '70px',
                    display: "inline-block",
                    paddingRight: '16px'
                  }}
                  src="data:image/png;base64,"
                  alt=""
                />
              </a>
            </td>
            <td style={{ paddingTop: '14px' }} colSpan={2}>
              <div style={{ marginLeft: '20px' }}>
                <a
                  style={{
                    textDecoration: "none",
                    display: "inline-block"
                  }}
                  href="https://app.sprinto.com/trust-center/view/b187f721-5db9-49a7-b2b0-ea2b59f9962c"
                  target="_blank"
                >
                  <img
                    style={{
                      height: '68px',
                      display: "inline-block",
                      marginRight: '6px',
                      border: "1px solid #346b80"
                    }}
                    src="data:image/png;base64,"
                    alt=""
                  />{" "}
                  <img
                    style={{
                      height: '68px',
                      display: "inline-block",
                      border: "1px solid #346b80"
                    }}
                    src="data:image/png;base64,"
                    alt=""
                  />
                </a>
              </div>
            </td>
          </tr>
          <tr>
            <td>
              <div style={{ fontSize: '11px' }}>
                Forbes: Top 200 with Global Business Potential
              </div>
            </td>
            <td colSpan={2}>
              <div style={{ fontSize: '11px', marginLeft: '20px' }}>
                An ISO 27001 | SOC2 Certified Company
              </div>
            </td>
          </tr>
          <tr>
            <td colSpan="3">
              <div style={{color: '#aaaaaa', fontSize: '10px'}}>
                Disclaimer: The content of this email is confidential and reserved for the recipient specified in this email
                only.<br/>
                Copyright 2024 Smarthub Innovation Private Limited. All rights reserved.
              </div>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </div>)
});

export default Preview;
